/*
$spacers: (
  0: 0,
  1: $spacer * 0.25,
  2: $spacer * 0.5,
  3: 27px,
  4: $spacer * 1.5,
  5: $spacer * 3,
  6: $spacer * 4,
  7: $spacer * 5,
  8: $spacer * 5.4,
  9: $spacer * 7,
  10: $spacer * 8,
);
*/
.sk-block.blocks .blocks-grid {
  display: grid;
}
.sk-block.blocks .blocks-grid.blocks-grid-2 .block, .sk-block.blocks .blocks-grid.blocks-grid-3 .block, .sk-block.blocks .blocks-grid.blocks-grid-4 .block {
  display: flex;
  flex-direction: column;
  gap: 25px;
}
.sk-block.blocks .blocks-grid-2 {
  grid-template-columns: 1fr 1fr;
}
.sk-block.blocks .blocks-grid-3 {
  grid-template-columns: 1fr 1fr 1fr;
}
.sk-block.blocks .blocks-grid-4 {
  grid-template-columns: 1fr 1fr 1fr 1fr;
}
.sk-block.blocks .blocks-grid-1 .block-content {
  display: flex;
  align-items: center;
  gap: 20px;
}
.sk-block.blocks .blocks-grid-1 .block-heading {
  flex-shrink: 0;
}
.sk-block.blocks.blocks-full .container {
  max-width: 100%;
  padding-left: 0;
  padding-right: 0;
}
.sk-block.blocks.blocks-full .blocks-grid {
  gap: 0;
}
.sk-block.blocks.blocks-full .block picture {
  display: flex;
  justify-content: center;
  align-items: center;
}
.sk-block.blocks.blocks-full .block picture img {
  object-fit: cover;
  max-width: none;
  height: 100%;
  width: 100%;
}
.sk-block.blocks.images-rounded .block img {
  border-radius: 15px;
}
.sk-block.blocks .content-collapse {
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  height: 18px;
  width: 18px;
  line-height: 18px;
  color: #17fcc4;
  font-size: 17px;
  text-align: center;
  background: #002ea6;
  border-radius: 50%;
  transition: all 0.3s ease;
}
.sk-block.blocks .content-collapse.visible {
  background: #17fcc4;
  transform: rotate(180deg);
}
.sk-block.blocks .content-collapse.visible::before {
  content: "-";
  position: absolute;
  top: -2px;
  bottom: 0;
  left: 0;
  right: 0;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-size: 19px;
  line-height: 14px;
  text-align: center;
  color: #002ea6;
  transition: all 0.3s ease;
}
.sk-block.blocks .blocks-type-icons .block .block-content h2:first-child, .sk-block.blocks .blocks-type-icons .block .block-content h3:first-child {
  margin-bottom: 10px;
}
.sk-block.blocks .blocks-type-icons .block img {
  width: 40px;
}
.sk-block.blocks.blocks-cards .block {
  padding: 25px 40px;
  background: #fff;
  border-radius: 15px;
}
.sk-block.blocks.blocks-cards .block img {
  width: 70px;
}
@media screen and (max-width: 1024px) {
  .sk-block.blocks .blocks-grid-3 {
    grid-template-columns: 1fr 1fr;
  }
  .sk-block.blocks .blocks-grid-4 {
    grid-template-columns: 1fr 1fr 1fr;
  }
  .sk-block.blocks.blocks-full .blocks-grid-4 {
    grid-template-columns: 1fr 1fr;
  }
}
@media screen and (max-width: 768px) {
  .sk-block.blocks .blocks-grid-4 {
    grid-template-columns: 1fr 1fr;
  }
  .sk-block.blocks .blocks-grid-1 .block-content {
    display: block;
  }
}
@media screen and (max-width: 640px) {
  .sk-block.blocks .blocks-grid {
    grid-template-columns: 1fr;
  }
}